import * as React from "react";
import styled from "styled-components";
import Navigation from "src/components/Navigation/Navigation";
import Divider from "src/components/Divider";
import Banner from "src/components/Banner";

export interface SharingProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 50px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;

            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
            letter-spacing: normal;
            color: #000000;
        }
        & > div:nth-child(2) {
            width: 80%;
            & > h1 {
                margin-bottom: 34px;
                font-size: 32px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #000000;
            }

            & > p {
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.71;
                letter-spacing: normal;
            }

            & > img {
                width: 100%;
                height: 430px;
                background-color: #aaa;
            }
        }
    }
`;

export default class Sharing extends React.Component<SharingProps, any> {
    public render() {
        return (
            <>
                <Navigation list={[{ name: "홈", link: "/" }, { name: "유니온", link: "/union/" }, { name: "나눔", link: "/sharing/" }]} />
                <Content>
                    <Banner>
                        <img src={require("../../assets/union/share-banner.png")} alt="" />
                    </Banner>
                    <Desc>
                        <div>
                            <div />
                            <div>
                                <img style={{ width: 425, height: 405 }} src={require("../../assets/union/sharing01.png")} alt="" />
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <h1 lang="en">
                                    Little effort for <br />a beautiful world with STELLA
                                </h1>
                                <p>
                                    유니언플레이스는 사람의 행복을 중요한 가치로 생각합니다.
                                    <br />
                                    우리는 개인의 행복이 사회를 풍요롭게 만든다는 사실에 주목했고, <br />
                                    고객에게 따뜻한 추억과 행복을 선물하기로 했습니다.
                                    <br />
                                    <br />
                                    하지만 우리의 이러한 작은 서비스로 세상을 바꾸기는 쉽지 않다고 생각했습니다.
                                    <br />
                                    그래서 우리는 우울증 및 자살예방 NGO 단체인&lt;스텔라재단&gt; 을 후원하기로 결정했습니다.
                                    <br />
                                    <br />
                                    우리는 '우울증'으로 고통받고, '행복'이라는 소중한 가치를 잃은 사람들에게 <br />
                                    희망과 기쁨을 선물하는 스텔라재단의 아름다운 선행을 함께 하고자 합니다.
                                    <br />
                                    <br />
                                    너와 내가 만나 새로운 공간, 새로운 시너지를 창출하는 유니언플레이스는 <br />
                                    당산동 사옥에서 스텔라재단과 함께 행복한 세상을 만들어 가고 있습니다.
                                    <br />
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <Divider color="black" />
                        </div>
                        <div>
                            <div />
                            <div>
                                <h1>‘유니언플레이스’는 &lt;스텔라재단&gt;을 후원합니다.</h1>
                                <p>
                                    *스텔라재단은 우울증 인식 개선 및 방지를 위한 비영리단체다.
                                    <br />
                                    한체대 출신 조재훈 대표가 우울증으로 어머니를 잃은 후 크라우드펀딩으로 설립. <br />
                                    우울증 치유와 자살방지 캠페인 등 행복을 찾기 위한 활동을 이어가고 있다.
                                    <br />
                                    <br />
                                    우리의 미션, 비전, 핵심가치, 기업문화는 모두 ‘사람의 행복’을 향해있습니다. <br />
                                    개인의 행복이 사회를 풍요롭게 만든다는 사실을 알기에,
                                    <br />
                                    유니언플레이스는 &lt;스텔라재단&gt;을 후원하기로 결정했습니다.
                                    <br />
                                    <br />
                                    '행복'이라는 소중한 가치를 잃은 사람들에게
                                    <br />
                                    희망과 기쁨을 선물하는 스텔라재단의 아름다운 선행! ‘유니언플레이스’도 함께 합니다.
                                    <br />
                                </p>
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



